.quote {
  float: left;
  width: 100%;
  font-style: italic;
  font-weight: 100;
  text-align: center;
  padding: 20px 0; }

.blur-bg {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 130%;
  height: 130%;
  transform: translate(-50%, -50%);
  filter: blur(30px);
  background-position: center;
  background-size: cover;
  z-index: 0; }

.gradient-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0; }

.opacityIn {
  animation: opacityIn 0.7s ease forwards;
  animation-delay: 0.5s; }

@keyframes opacityIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.inFromBottom {
  animation: inFromBottom 0.3s forwards;
  animation-delay: 0.5s; }

@keyframes inFromBottom {
  0%, 100% {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  from {
    transform: translate3D(0, 150%, 0);
    opacity: 0; }
  to {
    transform: translate3D(0, 0, 0);
    opacity: 1; } }

.outToLeft {
  animation: outToLeft 0.5s forwards; }

@keyframes outToLeft {
  0%, 100% {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  from {
    transform: translate3D(0, 0, 0);
    opacity: 1; }
  to {
    transform: translate3D(-80px, 0, 0);
    opacity: 0; } }

.leftToRight {
  animation: leftToRight 0.5s forwards;
  animation-delay: 0.5s; }

@keyframes leftToRight {
  0%, 100% {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  from {
    transform: translate3D(-50%, 0, 0);
    opacity: 0; }
  to {
    transform: translate3D(0, 0, 0);
    opacity: 1; } }

.rightToLeft {
  animation: rightToLeft 0.5s ease; }

@keyframes rightToLeft {
  0%, 100% {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  from {
    transform: translate3D(50%, 0, 0);
    opacity: 0; }
  to {
    transform: translate3D(0, 0, 0);
    opacity: 1; } }

.bounceIn {
  animation: bounceIn 0.5s; }

@keyframes bounceIn {
  0%, 100%, 20%, 40%, 60%, 80% {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  0% {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3); }
  20% {
    transform: scale3d(1.1, 1.1, 1.1); }
  40% {
    transform: scale3d(0.9, 0.9, 0.9); }
  60% {
    opacity: 1;
    transform: scale3d(1.03, 1.03, 1.03); }
  80% {
    transform: scale3d(0.97, 0.97, 0.97); }
  100% {
    opacity: 1;
    transform: scale3d(1, 1, 1); } }

